@mixin animation-mixin($name, $from, $to) {
    @keyframes #{$name} {
        0% {
            opacity: $from;
        }

        100% {
            opacity: $to;
        }
    }
}

@mixin transition($x...) {
    -webkit-transition: $x;
    -moz-transition: $x;
    -ms-transition: $x;
    -o-transition: $x;
    transition: $x;
}

@mixin cursor($cursor) {
    .c-#{$cursor} {
        cursor: $cursor;
    }
}

@mixin rotate($className, $degree) {
    .#{$className} {
        rotate: $degree;
    }
}