.sidebar {
    border-right: $global-border-width $global-border-style $global-border-color;
    transform: translate3d(0, 0, 0);
    backface-visibility: hidden;
    perspective: 1000;
    box-shadow: $navbar-shadow;
}

.sidebar-content {
    text-decoration: none;
    @extend .flex;
    @extend .flex-12;
    @extend .center;
    @extend .p-12;
}

.sidebar-show {
    opacity: 1;
    position: relative;

    height: calc(100vh - 60px);

    @include xs {
        height: auto;
    }

    visibility: visible;
    @include transition(opacity 0.5s ease-in);
}

.sidebar-hide {
    opacity: 0;
    position: absolute;
    visibility: hidden;
    @include transition(opacity 0.5s ease-out);
}