@import './responsiveness';
@import './flex';

// Responsive Flex
@include rFlex;

// Flex
@include flex-loop;

// Flex Align
@include flexAlign('center', center, center, center);
@include flexAlign('l-center', unset, center, flex-start);

// Flex Direction
@include flexDirection('fdr-reverse', row-reverse);

@import './alignment';

// Width
@include sizing-loop('.w-', 'width');
@include sizing-loop('.minw-', 'min-width');
@include sizing-loop('.maxw-', 'max-width');

// Height
@include sizing-loop('.h-', 'height');

// Margins
@include spacing-loop('.m-t-', 'margin-top');
@include spacing-loop('.m-r-', 'margin-right');
@include spacing-loop('.m-b-', 'margin-bottom');
@include spacing-loop('.m-l-', 'margin-left');
@include spacing-loop('.m-', 'margin');

// Paddings
@include spacing-loop('.p-t-', 'padding-top');
@include spacing-loop('.p-r-', 'padding-right');
@include spacing-loop('.p-b-', 'padding-bottom');
@include spacing-loop('.p-l-', 'padding-left');
@include spacing-loop('.p-', 'padding');

// Align
@include align(center);
@include align(left);
@include align(right);

// Display
@include display('inline', inline);
@include display('show', unset);
@include display('hide', none);

// Position
@include position($className: 'abs-right', $position: absolute, $top: 0, $right: 0);

@import './misc';

.fade-out {
    @include animation-mixin(fade-out, 0, 1);
    animation-name: fade-out;
    animation-duration: 0.3s;
    animation-fill-mode: forwards;
    will-change: opacity;
}

@include animation-mixin(fade-out, 0, 1);

// Cursor
@include cursor(pointer);

// Rotate
@include rotate('reverse', 180deg);

@import './component';