.navbar {
    background-color: $navbar-background-color;
    box-shadow: $navbar-shadow;
    height: $navbar-height;
    @extend .flex;
}

.navbar-menu {
    @extend .flex;
    @extend .r-flex-1;
    @extend .center;
    @extend .p-12;
}

.navbar-menu-icon {
    @extend .c-pointer;
}

.sidebar-menu-open {
    @extend .right;
}

.navbar-inner {
    @include flex-justify(space-between);
    width: 100%;
}

.navbar-items {
    @extend .flex;
    @extend .l-center;
    @extend .m-8;
}

.navitem {
    @include title;
    flex: 1 1 auto;
    overflow: hidden;
    color: $onBackground;
}

.navitem-title {
    @include title;
}

.navitem-right {
    @include flex-items($align: center, $justify: flex-end, $flex: 1 1 auto);
}