@mixin rFlex {
    $className: 'r-flex';
    $max: 20;
    $offset: 1;
    $unit: '%'; // Feel free to change the unit.
    $i: 0;

    @while $i <=$max {

        $i: $i + $offset;

        .r-flex-#{$i} {
            flex: 0 0 #{calc($i * 5) + $unit};

            @include xs {
                flex: 0 1 100%;
            }
        }
    }
}

@mixin flex-loop {
    $className: 'flex';
    $max: 12;
    $offset: 1;
    $unit: '%'; // Feel free to change the unit.
    $i: 0;

    @while $i <=$max {
        $i: $i + $offset;

        .flex-#{$i} {
            flex: 1 1 #{calc($i * 8.33333) + $unit};
        }
    }
}

@mixin flexAlign($className, $alignContent, $align, $justify ) {

    .#{$className} {
        align-content: $alignContent;
        align-items: $align;
        justify-content: $justify;
    }
}

@mixin flexDirection($className, $direction) {

    .#{$className} {
        flex-direction: row-reverse;
    }
}