@import './mixins/componentMixins';
@import './mixins/alignmentMixins';
@import './mixins/flexMixins';
@import './mixins/fontMixins';

.flex {
  @include flex;
}

.inline-flex {
  display: inline-flex;
}

.flex-wrap {
  flex-wrap: wrap;
}

// @include flex-loop('.flex-', 'flex');
@include nflex-loop('.flex-', 'nflex');


.sidenav {
  border-right: $global-border-width $global-border-style $global-border-color;
}

.btn {
  @include button($width: 100%);
}

.alert {
  @include button($width: auto);
  @extend .space-center-btw;
}

.btn-flat {
  @include button($flat: true);
}

.btn-label {
  @include button($flat: true, $minWidth: 160px);
  @include display($display: inline-block);
  @include textAlign($align: center);

  &>* {
    vertical-align: middle;
  }
}

.alert-flat {
  @include button($flat: true);
}

.btn-block {
  @include button($flat: true, $transform: false, $display: block);
}

.table {
  height: 400px;
  overflow-y: scroll;
}

.form {
  @include flex-justify(space-between);
}

.form-label {
  font-weight: $font-md;
  font-size: calc($global-font-size + 0.04rem);
  width: 100%;
}

.form-input {
  display: block;
  margin-top: 0.2rem;
  padding: $form-padding;
  font-size: $form-font-size;
  transition: $form-transition;
  border-radius: $form-border-radius;
  width: 100%;
  font-family: inherit;
  font-weight: 600;
  height: 38px;
}

.form-select {
  display: block;
  margin-top: 0.2rem;
  font-size: $form-font-size;
  transition: $form-transition;
  border-radius: $form-border-radius;
  width: 100%;
  font-family: inherit;
  font-weight: 600;
  appearance: none;
  background-image: url("data:image/svg+xml;charset=UTF-8,%3csvg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 24 24' fill='none' stroke='currentColor' stroke-width='2' stroke-linecap='round' stroke-linejoin='round'%3e%3cpolyline points='6 9 12 15 18 9'%3e%3c/polyline%3e%3c/svg%3e");
  background-repeat: no-repeat;
  background-position: right 0.6rem center;
  background-size: 1em;


}

.Select-control {
  border: none !important;
}

.form-textarea {
  @extend .form-input;
  box-sizing: border-box;
  resize: vertical;
}

.radio {
  @include button($transform: false, $width: auto);
  border-radius: 0px;
}

.radio-flat {
  @include button($transform: false, $width: auto, $flat: true);
  border-radius: 0px;
}

.dropdown {
  position: relative;
  z-index: 7;
}

.dropdown-content {
  width: 100%;
  position: absolute;
  left: 0;
  z-index: 10;
  @include dropdownContent();
}

.dropdown-content div {
  padding: 0.575rem 0.8rem;
  display: block;
  white-space: nowrap;

  &:hover {
    font-weight: 600;
    filter: brightness(1.25);
  }
}

.option {
  text-decoration: none;
  white-space: nowrap;
  background-color: white;
  border: 1px solid transparent;
  border-radius: 0.25rem;
  line-height: 1.5;
  font-size: 1rem;
  font-weight: 400;
  cursor: pointer;
  max-height: 200px;
  overflow-y: auto;
  transition: color 0.15s ease-in-out, background-color 0.15s ease-in-out,
    border-color 0.15s ease-in-out, box-shadow 0.15s ease-in-out;
  // box-shadow: rgb(0 0 0 / 10%) 0 2px 4px 0;
  // &:hover {
  //   box-shadow: rgb(0 0 0 / 15%) 0 3px 9px 0;
  // }
  display: block;
  white-space: nowrap;

  &:hover {
    font-weight: 600;
    filter: brightness(1.25);
  }
}

.iconWrapper {
  @extend .flex-center;
  margin: 1rem;
  padding: 1rem;
  box-shadow: rgb(0 0 0 / 15%) 0 3px 9px 0;
}

.accordion {
  .accordion-header {
    display: flex;
    // justify-content: space-between;
    padding-top: 0.75rem;
    padding-bottom: 0.75rem;
    padding-left: 0.75rem;
    padding-right: 0.75rem;
    position: relative;
    cursor: pointer;

    .header {
      margin: 0 1rem 0 0;
      font-size: 1.6rem;
      font-weight: 600;
      line-height: 1.25;
    }

    .icon {
      margin-left: auto;
    }
  }

  .accordion-content {
    padding-left: 1.5rem;
    padding-right: 1.5rem;
    padding-bottom: 1.5rem;
  }

  .accordion-wrapper {
    border-bottom: 1px solid $gray200;
  }

  .accordion-active {
    border-radius: 0.6rem;
    box-shadow: rgb(0 0 0 / 15%) 0 3px 9px 0;
  }

  .accordion-controls {
    display: flex;
    justify-content: flex-end;
    margin-bottom: 0.5rem;
  }
}

.overlay {
  .overlay-wrapper {
    position: fixed;
    width: 100%;
    height: 100vh;
    left: 0;
    top: 0;
    background: rgba(51, 51, 51, 0.7);
    z-index: 10;
  }

  .overlay-content {
    background-color: white;
    width: 100%;
    height: 100%;
    z-index: 11;
    padding: 20px;
  }
}

.login {
  box-shadow: rgb(0 0 0 / 15%) 0 3px 9px 0;
}

.profile {
  .profile-picture {
    position: relative;
  }

  .profile-content {
    position: absolute;
    border-radius: 2px;
    margin-top: 4px;
    z-index: 10;
  }
}

@keyframes rotate {
  from {
    transform: rotate(0deg);
  }

  to {
    transform: rotate(360deg);
  }
}

@keyframes anim {
  from {
    margin: 0px;
    opacity: 1;
  }

  to {
    margin: -4px;
    opacity: 0;
  }
}

.play {
  &::after {
    position: absolute;
    content: '';
    bottom: -2px;
    top: -2px;
    left: -2px;
    right: -2px;
    background-color: transparent;
    border-radius: 50%;
    border: 4px solid rgb(143, 21, 21);
    margin: 0px;
    opacity: 1;
    transform-origin: center;
    animation: anim 800ms linear infinite;
  }

  &::before {
    position: absolute;
    content: '';
    bottom: -2px;
    top: -2px;
    left: -2px;
    right: -2px;
    background-color: transparent;
    border-radius: 50%;
    border: 4px solid rgb(14, 9, 9);
    margin: 0px;
    opacity: 0.8;
    transform-origin: center;
    animation: anim 800ms linear 400ms infinite;
  }

}

.loader {
  display: inline-block;
  border-radius: 100%;
  margin: 2px;
  border: 2px solid hsl(199, 95%, 43%);
  border-bottom-color: transparent;
  margin: 2px;
  width: 22px;
  height: 22px;
  animation: rotate 0.75s linear infinite;
}

.loadingLayer {
  margin: 0;
  width: inherit;
  height: inherit;
  position: absolute;
  background-color: aliceblue;
}

.empty {
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.filterbar {
  height: 40px;
  overflow-y: auto;
  overflow-x: hidden;
  word-break: break-all;
}

.flex-card {
  @include flex-items($align: center);
  box-shadow: 0px 3px 6px rgba(0, 0, 0, 0.15);
  height: 100%;
}

.card {
  box-shadow: 0px 3px 6px rgba(0, 0, 0, 0.15);
  height: 100%;
}

.textOverflow {
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
  vertical-align: middle;
  @include display($display: inline-block);
}

progress {
  border-radius: 4px;
  width: 100%;
  height: 12px;
}

progress[value]::-webkit-progress-value {
  background-image:
    -webkit-linear-gradient(-45deg,
      transparent 33%, rgba(0, 0, 0, .1) 33%,
      rgba(0, 0, 0, .1) 66%, transparent 66%),
    -webkit-linear-gradient(top,
      rgba(255, 255, 255, .25),
      rgba(0, 0, 0, .25)),
    -webkit-linear-gradient(left, #10c, #f44);

  border-radius: 2px;
  background-size: 35px 20px, 100% 100%, 100% 100%;
}

.material-icons {
  font-family: 'Material Icons';
  font-weight: normal;
  font-style: normal;
  font-size: 24px;
  line-height: 1;
  letter-spacing: normal;
  text-transform: none;
  display: inline-block;
  white-space: nowrap;
  word-wrap: normal;
  direction: ltr;
  font-feature-settings: 'liga';
  -webkit-font-smoothing: antialiased;
  font-family: 'Material Icons';
  font-style: normal;
  font-weight: 400;
  src: url(https://fonts.gstatic.com/s/materialicons/v139/flUhRq6tzZclQEJ-Vdg-IuiaDsNc.woff2) format('woff2');
}

.modal {
  // display: none; /* Hidden by default */
  position: fixed;
  /* Stay in place */
  z-index: 1;
  /* Sit on top */
  padding-top: 100px;
  /* Location of the box */
  left: 0;
  top: 0;
  width: 100%;
  /* Full width */
  height: 100%;
  /* Full height */
  overflow: auto;
  /* Enable scroll if needed */
  background-color: rgb(0, 0, 0);
  /* Fallback color */
  background-color: rgba(0, 0, 0, 0.4);
  /* Black w/ opacity */
}

.modal-content {
  background-color: #fefefe;
  position: fixed;
  /* Stay in place */
  margin: auto;
  /* 15% from the top and centered */
  padding: 8px;
  border: 1px solid #888;
  width: 100%;
}

.close {
  color: #aaa;
  float: right;
  font-size: 28px;
  font-weight: bold;
}

.close:hover,
.close:focus {
  color: black;
  text-decoration: none;
  cursor: pointer;
}

.section {
  background-color: #fefefe;
  margin: auto;
  /* 15% from the top and centered */
  padding: 8px;
  z-index: 5;
  box-shadow: 0px 3px 6px rgba(0, 0, 0, 0.15);
}

.live-result-card-left {
  box-shadow: 0 2px 4px 0 #eeeeee;
  border: 1px solid #eeeeee;
  border-right: none;
  height: 180px;
}

.live-result-card {
  box-shadow: 0 2px 4px 0 #eeeeee;
  border: 1px solid #eeeeee;
  height: 180px;
}

.live-result-card-left-right {
  box-shadow: 0 2px 4px 0 #eeeeee;
  border: 1px solid #eeeeee;
  border-left: none;
  height: 140px;
}

.skeleton-label {
  font-size: 14px;
  font-weight: 600;
}

.skeleton-value {
  font-size: 15px;
  font-weight: 700;
}