@mixin position($className, $position, $top: null, $right: null, $bottom: null, $left: null) {
    .#{$className} {
        position: $position;
        top: $top;
        right: $right;
        bottom: $bottom;
        left: $left;
    }
}

@mixin align($align) {
    .t-#{$align} {
        text-align: $align;
    }
}

@mixin display($className, $position) {
    .#{$className} {
        display: $position;
    }
}

@mixin spacing-loop($className, $styleName) {
    $max: 48;
    $offset: 4;
    $unit: 'px'; // Feel free to change the unit.
    $i: 0;

    @while $i <=$max {
        #{$className + $i} {
            #{$styleName}: #{$i + $unit};
        }

        $i: $i + $offset;
    }
}

@mixin sizing-loop($className,
    $styleName,
    $unit: px,
    $max: 200,
    $offset: 10,
    $i: 0) {
    @while $i <=$max {
        #{$className + $i} {
            #{$styleName}: #{$i + $unit};
        }

        $i: $i + $offset;
    }
}

@mixin font-loop($className,
    $styleName,
    $unit: px,
    $max: 30,
    $offset: 1,
    $i: 0) {
    @while $i <=$max {
        #{$className + $i} {
            #{$styleName}: #{$i + $unit};
        }

        $i: $i + $offset;
    }
}